import React, { Component, ComponentType } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  createStyles,
  Grid,
  Theme,
  Typography,
  withStyles,
  WithStyles,
} from "@material-ui/core";

import { requestAllGuide } from "../../../_actions/guide.actions";
import { IGuide } from "../../../_types/guide.types";
import { AppActions } from "../../../_types";
import { AppState } from "../../../_types";
import { GuideCard } from "./GuideCard";
import { LoadingCircle } from "../../Layout/Loadings";

const useStyles = (theme: Theme) =>
  createStyles({
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 240px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: 0,
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface GuideProps extends WithStyles<typeof useStyles> {
  data: IGuide[];
  error: string;
  loading: boolean;
}

type Props = GuideProps & LinkDispatchProps;

class GuideList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllGuide();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <>
        <div className={classes.header}>
          <Typography variant="h3">مجموعه ها</Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={"تعداد: " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : data.length ? (
            data.map((guide: IGuide) => (
              <GuideCard key={guide.id} guide={guide} />
            ))
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </>
    );
  }
}

interface LinkDispatchProps {
  requestAllGuide: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): GuideProps => {
  return {
    classes: ownProps.classes,
    ...state.bank.guides,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllGuide: bindActionCreators(requestAllGuide, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(GuideList)) as unknown) as ComponentType<unknown>;
