import React, { Component, ComponentType } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  createStyles,
  Grid,
  Theme,
  Typography,
  withStyles,
  WithStyles,
} from "@material-ui/core";

import { ExerciseCard } from "./ExerciseCard";
import { AppState } from "../../../_types";
import { AppActions } from "../../../_types";
import { ILesson } from "../../../_types/lesson.types";
import { requestAllExercise } from "../../../_actions/exercise.actions";
import { LoadingCircle } from "../../Layout/Loadings";

const useStyles = (theme: Theme) =>
  createStyles({
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 240px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: 0,
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface ExerciseProps extends WithStyles<typeof useStyles> {
  data: ILesson[];
  error: string;
  loading: boolean;
}

type Props = ExerciseProps & LinkDispatchProps;

class ExerciseList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllExercise();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <>
        <div className={classes.header}>
          <Typography variant="h3">تمرین ها</Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={"تعداد: " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : data.length ? (
            data.map((exercise: ILesson) => (
              <ExerciseCard key={exercise.id} exercise={exercise} />
            ))
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </>
    );
  }
}

interface LinkDispatchProps {
  requestAllExercise: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): ExerciseProps => {
  return {
    classes: ownProps.classes,
    ...state.bank.exercises,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllExercise: bindActionCreators(requestAllExercise, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(ExerciseList)) as unknown) as ComponentType<unknown>;
