import React, { Component, ComponentType } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  Grid,
  Typography,
  WithStyles,
  createStyles,
  Theme,
  withStyles,
} from "@material-ui/core";

import { CourseCard } from "./CourseCard";
import { AppState } from "../../../_types";
import { AppActions } from "../../../_types";
import { ICourse } from "../../../_types/course.types";
import { requestAllCourse } from "../../../_actions/course.actions";
import { LoadingCircle } from "../../Layout/Loadings";

const useStyles = (theme: Theme) =>
  createStyles({
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 240px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: 0,
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface CourseProps extends WithStyles<typeof useStyles> {
  data: ICourse[];
  error: string;
  loading: boolean;
}

type Props = CourseProps & LinkDispatchProps;

class CourseList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllCourse();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <>
        <div className={classes.header}>
          <Typography variant="h3">آموزش ها</Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={"تعداد: " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : data.length ? (
            data.map((course: ICourse) => (
              <CourseCard key={course.id} course={course} />
            ))
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </>
    );
  }
}

interface LinkDispatchProps {
  requestAllCourse: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): CourseProps => {
  return {
    classes: ownProps.classes,
    ...state.bank.courses,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllCourse: bindActionCreators(requestAllCourse, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(CourseList)) as unknown) as ComponentType<unknown>;
