import { Dispatch } from "react";
import {
  FAILURE_FAVORITES,
  REQUEST_FAVORITES,
  SUCCESS_FAVORITES,
} from "../_types/favorite.types";
import { API } from "../_helpers/api";
import { AppState } from "../_types";
import { AppActions } from "../_types";
import { getTheTime } from "../_helpers/constants";
import { errorHandler } from "../components/Layout/SnackBar/alert";

export const requestAllFavorite = () => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const favoriteState = getState().user.favorites;
    const currentTime = getTheTime();
    const resetTime = currentTime - favoriteState.nextLoad;
    if (favoriteState.data && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_FAVORITES });

    API.get(`/lesson/v2`)
      .then(res => dispatch({
        type: SUCCESS_FAVORITES, payload: {
          data: res.data,
          nextLoad: getTheTime(10)
        }
      }))
      .catch(err => errorHandler(err, FAILURE_FAVORITES))
  }
)
