import { Dispatch } from "react";
import { ThunkDispatch } from "redux-thunk";
import {
  FAILURE_EXERCISE,
  REQUEST_EXERCISE,
  SUCCESS_EXERCISE,
  FAILURE_EXERCISES,
  REQUEST_EXERCISES,
  SUCCESS_EXERCISES,
  SAVE_EXERCISES,
} from "../_types/exercise.types";
import { errorHandler } from "../components/Layout/SnackBar/alert";
import { getTheTime, PathFinder } from "../_helpers/constants";
import { requestCourse } from "./course.actions";
import { requestGuide } from "./guide.actions";
import { AppActions } from "../_types";
import { API } from "../_helpers/api";
import { AppState } from "../_types";
import { ENTER_NOTEMODE } from "../_types/player.types";

export const requestAllExercise = () => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const exerciseState = getState().bank.exercises;
    const currentTime = getTheTime();
    const resetTime = currentTime - exerciseState.nextLoad;
    if (exerciseState.data && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_EXERCISES });

    API.get(`/lesson/v2?course=125`)
      .then(res => dispatch({
        type: SUCCESS_EXERCISES, payload: {
          data: res.data,
          nextLoad: getTheTime(10)
        }
      }))
      .catch(err => errorHandler(err, FAILURE_EXERCISES))
  }
)

export const requestExercise = (path: PathFinder) => (
  (dispatch: ThunkDispatch<AppState, undefined, AppActions>, getState: () => AppState) => {
    const found = getState().bank.exercises.data.find(exercise => exercise.id === path.exercise);
    if (found) {
      dispatch({ type: ENTER_NOTEMODE })
      dispatch({ type: SUCCESS_EXERCISE, data: found })
    }
    else if (path.guide) {
      dispatch(requestGuide(path))
    }
    else if (path.course) {
      dispatch(requestCourse(path))
    }
    else if (!found) {
      dispatch({ type: REQUEST_EXERCISE });
      API.get(`/lesson/v2?id=${path.exercise}`)
        .then(res => {
          dispatch({ type: ENTER_NOTEMODE })
          dispatch({ type: SUCCESS_EXERCISE, data: res.data[0] })
          dispatch({ type: SAVE_EXERCISES, data: res.data })
        })
        .catch(err => errorHandler(err, FAILURE_EXERCISE))
    }
  }
)