import React, { Component, ComponentType } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";
import {
  Chip,
  Grid,
  Typography,
  Theme,
  createStyles,
  WithStyles,
  withStyles,
} from "@material-ui/core";

import { requestAllLesson } from "../../../_actions/lesson.actions";
import { ILesson } from "../../../_types/lesson.types";
import { AppActions } from "../../../_types";
import { AppState } from "../../../_types";
import { LessonCard } from "./LessonCard";
import { LoadingCircle } from "../../Layout/Loadings";

const useStyles = (theme: Theme) =>
  createStyles({
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 240px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: 0,
    },
    autoMargin: {
      margin: "auto",
    },
  });

interface LessonProps extends WithStyles<typeof useStyles> {
  data: ILesson[];
  error?: string;
  loading?: boolean;
}

type Props = LessonProps & LinkDispatchProps;

class LessonList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllLesson();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <>
        <div className={classes.header}>
          <Typography variant="h3">درس ها</Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={"تعداد: " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : data.length ? (
            data.map((lesson: ILesson) => (
              <LessonCard key={lesson.id} lesson={lesson} />
            ))
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
        </Grid>
      </>
    );
  }
}

interface LinkDispatchProps {
  requestAllLesson: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): LessonProps => {
  return {
    classes: ownProps.classes,
    ...state.bank.lessons,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllLesson: bindActionCreators(requestAllLesson, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(LessonList)) as unknown) as ComponentType<unknown>;
