import React, { useState, useEffect } from "react";
import {
  Tab,
  Box,
  Tabs,
  AppBar,
  makeStyles,
  Paper,
  Theme,
  useMediaQuery,
} from "@material-ui/core";
import {
  LibraryMusic,
  QueueMusic,
  MusicNote,
  EmojiSymbols,
  Favorite,
} from "@material-ui/icons";

import GuideList from "./Guide/GuideList";
import LessonList from "./Lesson/LessonList";
import CourseList from "./Course/CourseList";
import FavoriteList from "./Favorite/FavoriteList";
import ExerciseList from "./Exercise/ExerciseList";
import { store } from "../../_store/store.config";
import { push } from "connected-react-router";

const pages = [
  {
    link: "/guide",
    icon: <LibraryMusic />,
    label: "GUIDES",
    child: <GuideList />,
  },
  {
    link: "/course",
    icon: <QueueMusic />,
    label: "COURSES",
    child: <CourseList />,
  },
  {
    link: "/lesson",
    icon: <MusicNote />,
    label: "LESSONS",
    child: <LessonList />,
  },
  {
    link: "/exercise",
    icon: <EmojiSymbols />,
    label: "EXERCISES",
    child: <ExerciseList />,
  },
  {
    link: "/favorite",
    icon: <Favorite />,
    label: "FAVORITES",
    child: <FavoriteList />,
  },
];

const pageName = (num: number) => {
  return pages[num].link;
};
const pageNumber = (name: string) => {
  const obj = pages.find((page) => page.link === name);
  if (obj) return pages.indexOf(obj);
  return 0;
};

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: "100vw",
  },
  tabControl: {
    [theme.breakpoints.down(880)]: {
      minWidth: 150,
    },
    [theme.breakpoints.down(830)]: {
      minWidth: 140,
    },
    [theme.breakpoints.down(780)]: {
      minWidth: 120,
    },
    [theme.breakpoints.down(680)]: {
      minWidth: 100,
    },
    [theme.breakpoints.down(500)]: {
      minWidth: 80,
    },
    [theme.breakpoints.down(370)]: {
      minWidth: 65,
    },
    [theme.breakpoints.down(320)]: {
      minWidth: 55,
    },
  },
  tabPanel: {
    padding: theme.spacing(2),
  },
}));

export default function LearningPage() {
  const classes = useStyles();

  const loggedIn = store.getState().auth.loggedIn;
  const pathName = store.getState().router.location.pathname;

  const [value, setValue] = useState(pageNumber(pathName));

  useEffect(() => {
    if (pathName === "/favorite" && !loggedIn)
      store.dispatch(push(pageName(0)));
    else setValue(pageNumber(pathName));
  }, [pathName, loggedIn]);

  const matches = useMediaQuery("(min-width:500px)");

  const handleChange = (newValue: number) => {
    setValue(newValue);
    store.dispatch(push(pageName(newValue)));
  };

  return (
    <>
      <AppBar position="static">
        <Paper square>
          <Tabs
            value={value}
            onChange={(_, e) => handleChange(e)}
            variant="scrollable"
            indicatorColor="secondary"
            textColor="secondary"
            scrollButtons="desktop"
            aria-label="icon label tabs"
          >
            {pages.map((page, i) =>
              i !== 4 ? (
                <Tab
                  className={classes.tabControl}
                  key={page.label}
                  icon={page.icon}
                  label={matches ? page.label : null}
                  id={`full-width-tab-${i}`}
                  aria-controls={`full-width-tabpanel-${i}`}
                />
              ) : (
                loggedIn && (
                  <Tab
                    className={classes.tabControl}
                    key={page.label}
                    icon={page.icon}
                    label={matches ? page.label : null}
                    id={`full-width-tab-${i}`}
                    aria-controls={`full-width-tabpanel-${i}`}
                  />
                )
              )
            )}
          </Tabs>
        </Paper>
      </AppBar>
      {pages.map((page, i) => (
        <div
          key={page.label + i}
          role="tabpanel"
          hidden={value !== i}
          id={`full-width-tabpanel-${i}`}
          aria-labelledby={`full-width-tab-${i}`}
        >
          {value === i && <Box className={classes.tabPanel}>{page.child}</Box>}
        </div>
      ))}
    </>
  );
}
