import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  CardContent,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../../_store/store.config";
import { IGuide } from "../../../_types/guide.types";
import { thumbnailsFinder } from "../../../_helpers/constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 300,
      maxHeight: 360,
      margin: theme.spacing(2),
      [theme.breakpoints.down(350)]: {
        margin: theme.spacing(1),
      },
    },
    cardAction: {
      justifyContent: "space-between",
      margin: "inherit",
    },
    content: {
      minHeight: 150,
    },
  })
);

type Props = {
  guide: IGuide;
};

export const GuideCard: React.FC<Props> = ({ guide }) => {
  const classes = useStyles();
  const { title, thumbnails, description, teacher } = guide;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    store.dispatch(push(`/guide/${guide.id}`));
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          component="img"
          alt={title}
          height="140"
          image={thumbnailsFinder(thumbnails)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
          از {teacher}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
          ادامه مطلب
        </Button>
      </CardActions>
    </Card>
  );
};
