import { Dispatch } from "react";
import {
  SAVE_GUIDES,
  FAILURE_GUIDE,
  REQUEST_GUIDE,
  SUCCESS_GUIDE,
  FAILURE_GUIDES,
  REQUEST_GUIDES,
  SUCCESS_GUIDES,
  GuideSelect,
} from "../_types/guide.types";
import { AppState } from "../_types";
import { API } from "../_helpers/api";
import { AppActions } from "../_types";
import { errorHandler } from "../components/Layout/SnackBar/alert";
import { ILesson } from "../_types/lesson.types";
import { getTheTime, PathFinder } from "../_helpers/constants";
import { SAVE_EXERCISES } from "../_types/exercise.types";
import { ICourse, SAVE_COURSES } from "../_types/course.types";
import { requestExercise } from "./exercise.actions";
import { requestCourse } from "./course.actions";
import { ThunkDispatch } from "redux-thunk";

export const requestAllGuide = () => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const guideState = getState().bank.guides;
    const currentTime = getTheTime();
    const resetTime = currentTime - guideState.nextLoad;
    if (guideState.data && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_GUIDES });

    API.get(`/lesson/v2/guide`)
      .then(res => dispatch({
        type: SUCCESS_GUIDES, payload: {
          data: res.data,
          nextLoad: getTheTime(10)
        }
      }))
      .catch(err => errorHandler(err, FAILURE_GUIDES))
  }
)

export const requestGuide = (path: PathFinder) => (
  (dispatch: ThunkDispatch<AppState, undefined, AppActions>, getState: () => AppState) => {
    const { guide, ...newPath } = path;
    const found = getState().bank.guides.data.find(guide => guide.id === path.guide);
    if (found) {

      dispatch({ type: SUCCESS_GUIDE, data: found });
      dispatch({ type: SAVE_COURSES, data: addCourses(found.guide_select) });
      dispatch({ type: SAVE_EXERCISES, data: addExercises(found.guide_select) });
      if (path.course) dispatch(requestCourse(newPath));
      if (path.exercise) dispatch(requestExercise(newPath));
    } else {

      dispatch({ type: REQUEST_GUIDE });
      API.get(`/lesson/v2/guide?id=${path.guide}`)
        .then(res => {
          dispatch({ type: SUCCESS_GUIDE, data: res.data[0] });
          dispatch({ type: SAVE_GUIDES, data: res.data });
          dispatch({ type: SAVE_COURSES, data: addCourses(res.data[0].guide_select) });
          dispatch({ type: SAVE_EXERCISES, data: addExercises(res.data[0].guide_select) });
          if (path.course) dispatch(requestCourse(newPath));
          if (path.exercise) dispatch(requestExercise(newPath));
        })
        .catch(err => errorHandler(err, FAILURE_GUIDE));
    }
  }
)

const addCourses = (guide_select: GuideSelect[]) => {
  const added: ICourse[] = []
  guide_select.map((course) => course.course?.map(lesson => added.push(lesson)))
  return added;
};
const addExercises = (guide_select: GuideSelect[]) => {
  const added: ILesson[] = []
  guide_select.map((course) => course.exercise?.map(exercise => added.push(exercise)))
  return added;
}