import { Dispatch } from "react";
import {
  FAILURE_LESSON,
  REQUEST_LESSON,
  SUCCESS_LESSON,
  FAILURE_LESSONS,
  REQUEST_LESSONS,
  SUCCESS_LESSONS,
  SAVE_LESSONS,
} from "../_types/lesson.types";
import { API } from "../_helpers/api";
import { AppState } from "../_types";
import { AppActions } from "../_types";
import { getTheTime, PathFinder } from "../_helpers/constants";
import { errorHandler } from "../components/Layout/SnackBar/alert";
import { requestGuide } from "./guide.actions";
import { requestCourse } from "./course.actions";
import { ThunkDispatch } from "redux-thunk";

export const requestAllLesson = () => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const lessonState = getState().bank.lessons;
    const currentTime = getTheTime();
    const resetTime = currentTime - lessonState.nextLoad;
    if (lessonState.data && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_LESSONS });

    API.get(`/lesson/v2`)
      .then(res => dispatch({
        type: SUCCESS_LESSONS, payload: {
          data: res.data,
          nextLoad: getTheTime(10)
        }
      }))
      .catch(err => errorHandler(err, FAILURE_LESSONS))
  }
)

export const requestLesson = (path: PathFinder) => (
  (dispatch: ThunkDispatch<AppState, undefined, AppActions>, getState: () => AppState) => {
    const found = getState().bank.lessons.data.find(lesson => lesson.id === path.lesson);

    if (found) {
      dispatch({ type: SUCCESS_LESSON, data: found })
    }
    else if (path.guide) {
      dispatch(requestGuide(path))
    }
    else if (path.course) {
      dispatch(requestCourse(path))
    }
    else if (!found) {
      dispatch({ type: REQUEST_LESSON });
      API.get(`/lesson/v2?id=${path.lesson}`)
        .then(res => {
          dispatch({ type: SUCCESS_LESSON, data: res.data[0] })
          dispatch({ type: SAVE_LESSONS, data: res.data })
        })
        .catch(err => errorHandler(err, FAILURE_LESSON))
    }
  }
)

